.content-body-wrapper {
    background-color: #cecbcb;
    &-inner {
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-template-rows: 30px 150px 150px 420px 500px 50px 450px 60px 50px 700px 0px 50px 650px 50px 200px 70px;
        grid-gap: 15px;
        margin: 0 auto;
        width: 1000px;
        padding: 10px;
        background-color: white;
    }
}